<!-- 首页tab组件 -->
<template>
	<view class="indexTab">
		<scroll-view class="scroll-content" scroll-x="true" :scroll-into-view="scrollIntoIndex">
			<view class="scroll-item" v-for="(item,index) in tabBar" :key="index" :id="'top'+index">
				<text :class="tabBarIndex == index ? 'f-active-color' : 'f-color'" @click="changeTab(index)">{{item.name}}</text>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				tabContentHeight: 0,
				//顶部导航跟随滑动索引
				scrollIntoIndex: 'top0',
				tabBarIndex: 0,
				tabBar : [
					{'name' : '推荐'},
					{'name' : '今日热点'},
					{'name' : '推荐店铺'},
					{'name' : '运动户外'},
					{'name' : '猜你喜欢'},
					{'name' : '热款爆品'},
					{'name' : '买了到家'},
				]
			}
		},
		components: {
			
		},
		methods: {
			
		},
		onLoad() {
			
		}
	}
</script>

<style>
	.scroll-content {
		width: 100%;
		height: 80rpx;
		white-space: nowrap;
	}
	.scroll-item {
		display: inline-block;
		padding: 10rpx 20rpx;
	}
	.f-active-color {
		text-decoration: underline;
	}
</style>